// client/main.js
import { RoomClient } from "./RoomClient.js";
import { MediaPipeModule } from "./modules/MediaPipeModule.js";
import { UIManager } from "./UIManager.js";

// --- Ïï†ÌîåÎ¶¨ÏºÄÏù¥ÏÖò ÏãúÏûë ---
document.addEventListener("DOMContentLoaded", () => {
  console.log("üöÄ Application starting...");

  const uiManager = new UIManager();
  const roomClient = new RoomClient(uiManager);

  // ‚úÖ RoomClientÍ∞Ä Î∞©ÏÜ°ÌïòÎäî Ïù¥Î≤§Ìä∏Î•º Íµ¨ÎèÖÌïòÏó¨ UIManagerÏóê ÏûëÏóÖÏùÑ ÏßÄÏãúÌï©ÎãàÎã§.
  roomClient.on("new-consumer", (consumer) => {
    console.log("üéß Event: new-consumer -> UI Manager adding remote track.");
    uiManager.addRemoteTrack(consumer.track, consumer.producerId);
  });

  roomClient.on("producer-closed", (producerId) => {
    console.log(
      "üéß Event: producer-closed -> UI Manager removing remote track."
    );
    uiManager.removeRemoteTrack(producerId);
  });

  // ‚ùóÔ∏èÌïµÏã¨: ÏÇ¨Ïö©ÏûêÍ∞Ä 'Ï°∏Ïùå Í∞êÏßÄ Î™®Îìà'ÏùÑ Íµ¨Îß§ÌñàÎäîÏßÄ Ïó¨Î∂Ä (ÎÇòÏ§ëÏóêÎäî ÏÑúÎ≤ÑÏóêÏÑú Î∞õÏïÑÏò¨ Í∞í)
  const userHasAiModule = false;

  if (userHasAiModule) {
    const videoElement = document.getElementById("localVideo");
    const aiModule = new MediaPipeModule(videoElement);

    console.log("ü§ñ AI Module will be initialized.");

    // --- ÏßÄÌúòÏûê(main.js)Í∞Ä Í∞Å Î™®ÎìàÏùò Ïù¥Î≤§Ìä∏Î•º Ïó∞Í≤∞(Íµ¨ÎèÖ)Ìï¥Ï§çÎãàÎã§. ---

    // 1. RoomClientÍ∞Ä 'Î°úÏª¨ Ïä§Ìä∏Î¶º Ï§ÄÎπÑ ÏôÑÎ£å'Î•º Î∞©ÏÜ°ÌïòÎ©¥, AI Î™®ÎìàÏù¥ Î∂ÑÏÑùÏùÑ ÏãúÏûëÌï©ÎãàÎã§.
    roomClient.on("localStreamReady", () => {
      console.log("üéß Event: localStreamReady -> AI Module starting analysis.");
      aiModule.start();
    });

    // 2. AI Î™®ÎìàÏù¥ 'ÎûúÎìúÎßàÌÅ¨ ÏóÖÎç∞Ïù¥Ìä∏'Î•º Î∞©ÏÜ°ÌïòÎ©¥, UI Îß§ÎãàÏ†ÄÍ∞Ä ÌôîÎ©¥Ïóê Í∑∏Î¶ºÏùÑ Í∑∏Î¶ΩÎãàÎã§.
    aiModule.on("landmarksUpdate", (landmarks) => {
      uiManager.drawFaceMesh(landmarks);
    });

    // 3. AI Î™®ÎìàÏù¥ 'ÏÉÅÌÉú Î≥ÄÍ≤Ω'ÏùÑ Î∞©ÏÜ°ÌïòÎ©¥, RoomClientÍ∞Ä ÏÑúÎ≤ÑÎ°ú Îç∞Ïù¥ÌÑ∞Î•º Ï†ÑÏÜ°Ìï©ÎãàÎã§.
    aiModule.on("drowsinessUpdate", (data) => {
      console.log("üéß Event: drowsinessUpdate -> Sending status to server.");
      roomClient.sendPeerStatus(data);
    });
    aiModule.on("absenceUpdate", (data) => {
      console.log("üéß Event: absenceUpdate -> Sending status to server.");
      roomClient.sendPeerStatus(data);
    });

    // 4. AI Î™®ÎìàÏóêÏÑú ÏóêÎü¨Í∞Ä Î∞úÏÉùÌïòÎ©¥ ÏΩòÏÜîÏóê Ï∂úÎ†•Ìï©ÎãàÎã§.
    aiModule.on("error", (error) => {
      console.error("üî• AI Module Error:", error);
    });
  }

  // ÌôîÏÉÅÌöåÏùò ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏Ïùò Î™®Îì† Ï§ÄÎπÑÎ•º ÏãúÏûëÌï©ÎãàÎã§.
  roomClient.join();
});
